import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react"

export const authApi = createApi({
    reducerPath: "authApi",
    baseQuery: fetchBaseQuery({ baseUrl: `https://irg.onrender.com/api/auth`, credentials: "include" }),
    endpoints: (builder) => {
        return {
            registerAdmin: builder.mutation({
                query: adminData => {
                    return {
                        url: "/register-admin",
                        method: "POST",
                        body: adminData
                    }
                },
            }),
            loginAdmin: builder.mutation({
                query: adminData => {
                    return {
                        url: "/login-admin",
                        method: "POST",
                        body: adminData
                    }
                },
            }),
            logoutAdmin: builder.mutation({
                query: userData => {
                    return {
                        url: "/logout-admin",
                        method: "POST",
                        body: userData
                    }
                },
            }),

            registerUser: builder.mutation({
                query: adminData => {
                    return {
                        url: "/register-user",
                        method: "POST",
                        body: adminData
                    }
                },
            }),
            loginUser: builder.mutation({
                query: adminData => {
                    return {
                        url: "/login-user",
                        method: "POST",
                        body: adminData
                    }
                },
            }),
            logoutUser: builder.mutation({
                query: userData => {
                    return {
                        url: "/logout-user",
                        method: "POST",
                        body: userData
                    }
                },
            }),

        }
    }
})

export const {
    useLoginAdminMutation,
    useLoginUserMutation,
    useLogoutAdminMutation,
    useLogoutUserMutation,
    useRegisterAdminMutation,
    useRegisterUserMutation } = authApi
