import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react"

export const userApi = createApi({
    reducerPath: "userApi",
    baseQuery: fetchBaseQuery({ baseUrl: `https://irg.onrender.com/api/user`, credentials: "include" }),
    tagTypes: ["property"],
    endpoints: (builder) => {
        return {
            getProperty: builder.query({
                query: () => {
                    return {
                        url: "/get-property",
                        method: "GET"
                    }
                },
                providesTags: ["property"]
            }),
            getAllProperty: builder.query({
                query: () => {
                    return {
                        url: "/getAll-property",
                        method: "GET"
                    }
                },
                providesTags: ["property"]
            }),


        }
    }
})

export const { useGetAllPropertyQuery, useGetPropertyQuery } = userApi
